@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";
  font-display: swap;
  src: url("../fonts/.woff2") format("woff2"), url("../fonts/.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  scroll-behavior: smooth;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #f9fbfd;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

h2 {
  margin-top: 0;
  margin-bottom: 49px;
  font-size: 28px;
  font-weight: 600;
  line-height: 42px;
  text-align: center;
}
@media (max-width: 1023px) {
  h2 {
    font-size: 26px;
    line-height: 36px;
  }
}
@media (max-width: 767px) {
  h2 {
    margin-bottom: 30px;
    font-size: 24px;
    line-height: 34px;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}

.btn-back-to-top {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  right: 50px;
  bottom: 50px;
  width: 50px;
  height: 50px;
  border: 1px solid transparent;
  border-radius: 50%;
  background-color: #011c40;
  transition: all ease-in-out 0.3s;
  cursor: pointer;
}
@media (max-width: 767px) {
  .btn-back-to-top {
    width: 35px;
    height: 35px;
    right: 35px;
    bottom: 50px;
  }
}

.btn-back-to-top:hover {
  background-color: #ff1553;
  box-shadow: 0 0 5px 1px #011c40;
}

.btn-back-to-top::before,
.btn-back-to-top::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 3px;
}

.btn-back-to-top::before {
  width: 5px;
  height: 19px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .btn-back-to-top::before {
    width: 2px;
    height: 19px;
  }
}

.btn-back-to-top::after {
  width: 15px;
  height: 15px;
  border-top: 5px solid #f9fbfd;
  border-left: 5px solid #f9fbfd;
  transform: translate(-50%, -50%) rotate(45deg);
}
@media (max-width: 767px) {
  .btn-back-to-top::after {
    width: 12px;
    height: 12px;
    top: 15px;
    border-width: 3px;
  }
}

.btn-back-to-top--show {
  visibility: visible;
  opacity: 1;
}

.btn-back-to-top--green {
  background-color: green;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.ui-kit-wrapper {
  padding: 50px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  color: #011c40;
  background-color: #333333;
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  width: 100%;
  min-height: 40px;
  font-size: 14px;
  line-height: 21px;
  font-weight: 600;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
  transition: 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    background-color: #fff352;
  }
}
.btn.focus-visible:focus {
  background-color: #fff352;
}

.checkbox {
  position: relative;
}
.checkbox input {
  appearance: none;
  position: absolute;
}
.checkbox input:focus-visible + label {
  border-color: #011c40;
  transition: 0.3s;
}
.checkbox label:hover {
  color: #fff352;
}
.checkbox label {
  padding-left: 25px;
  display: inline-block;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 10px;
  line-height: 13px;
  color: #f9fbfd;
  cursor: pointer;
  transition: 0.3s;
}
.checkbox input:checked + label .checkbox__icon::after {
  border-color: #011c40;
  opacity: 1;
}
.checkbox .checkbox__icon {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
  z-index: 1;
  transition: 0.3s;
}
.checkbox .checkbox__icon::after {
  position: absolute;
  content: "";
  top: 3px;
  left: 20%;
  width: 10px;
  height: 7px;
  border-left: 1px solid transparent;
  border-bottom: 1px solid transparent;
  transform: rotate(-45deg);
  z-index: 2;
  transition: 0.3s;
  opacity: 0;
}

.card-advantage {
  padding: 80px 30px 45px;
  height: 100%;
  background-color: #f9fbfd;
  background-repeat: no-repeat;
  background-position: top 30px left 30px;
}
@media (max-width: 1023px) {
  .card-advantage {
    padding: 69px 17px 20px;
    background-position: top 20px left 20px;
    background-size: auto 26px;
  }
}
@media (max-width: 767px) {
  .card-advantage {
    min-height: 250px;
    background-position: top 25px left 20px;
  }
}
.card-advantage svg {
  fill: #011c40;
  margin-bottom: 13px;
}
@media (max-width: 1023px) {
  .card-advantage svg {
    margin-bottom: 10px;
    width: 36px;
    height: 26px;
  }
}
@media (max-width: 767px) {
  .card-advantage svg {
    margin-bottom: 15px;
  }
}
.card-advantage h3 {
  margin: 0;
  margin-bottom: 8px;
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
}
@media (max-width: 1023px) {
  .card-advantage h3 {
    line-height: 25px;
  }
}
.card-advantage p {
  margin: 0;
  font-size: 14px;
  line-height: 20px;
}

.card-cruise {
  position: relative;
  min-height: 386px;
  color: #f9fbfd;
}
.card-cruise h4 {
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
}
.card-cruise img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card-cruise .card-cruise__front,
.card-cruise .card-cruise__back {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  backface-visibility: hidden;
  transition: transform 0.6s ease;
}
.card-cruise .card-cruise__front {
  transform: perspective(600px) rotateY(0deg);
  cursor: auto;
}
.card-cruise .card-cruise__front h4 {
  position: absolute;
  margin: 0;
  bottom: 23px;
  left: 30px;
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .card-cruise .card-cruise__front h4 {
    bottom: 30px;
    left: 35px;
  }
}
@media (max-width: 767px) {
  .card-cruise .card-cruise__front h4 {
    bottom: 20px;
    left: 25px;
  }
}
.card-cruise .card-cruise__back {
  display: flex;
  flex-direction: column;
  padding: 30px 25px;
  overflow: auto;
  transform: perspective(600px) rotateY(180deg);
  background-color: #011c40;
}
.card-cruise .card-cruise__back h4 {
  margin: 0;
  margin-bottom: 12px;
}
.card-cruise .card-cruise__back dl {
  margin: 0;
  margin-bottom: 25px;
}
.card-cruise .card-cruise__back div:not(:last-child) {
  margin-bottom: 12px;
}
.card-cruise .card-cruise__back dt {
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
}
.card-cruise .card-cruise__back dd {
  margin: 0;
  font-size: 14px;
  color: #d8d8d9;
  line-height: 20px;
}

.card-cruise:hover > .card-cruise__front,
.card-cruise:focus-within > .card-cruise__front {
  transform: perspective(600px) rotateY(-180deg);
}

.card-cruise:hover > .card-cruise__back,
.card-cruise:focus-within > .card-cruise__back {
  transform: perspective(600px) rotateY(0deg);
}

.card-cruise__btn {
  margin-top: auto;
}

.logo {
  position: relative;
  margin-right: 20px;
  transition: all 0.5s ease 0s;
  z-index: 5;
  flex-shrink: 0;
  align-self: flex-start;
}
@media (max-width: 1023px) {
  .logo img {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo img {
    width: 42px;
    height: 22px;
  }
}

.menu__list {
  margin: 0;
  padding: 0;
  display: flex;
  column-gap: 20px;
  list-style: none;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .menu__list {
    column-gap: 12px;
  }
}
@media (max-width: 767px) {
  .menu__list {
    display: block;
    margin-top: 25px;
    padding-bottom: 25px;
    margin-bottom: 0;
  }
}

.menu__list li {
  margin: 0;
}
@media (max-width: 767px) {
  .menu__list li {
    margin-bottom: 20px;
  }
}

.menu__list li:hover .menu__link {
  color: #011c40;
  background-color: #fff352;
  padding: 5px 10px;
  border-radius: 5px;
}

.menu__link {
  padding: 5px;
  color: #f9fbfd;
  text-decoration: none;
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
  transition: 0.3s;
}
@media (max-width: 1023px) {
  .menu__link {
    font-size: 14px;
    line-height: 17px;
  }
}

.menu__link:active {
  color: #532121;
}

.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 3;
  padding: 35px 0;
  transition: 0.5s;
}
@media (max-width: 1023px) {
  .header {
    padding: 28px 0;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 19px 0;
    min-height: 60px;
  }
}
.header--is-scrolled {
  background-color: rgba(37, 37, 37, 0.6352941176);
}

.header__logo--dark {
  display: none;
}

.header__body {
  position: relative;
  display: flex;
  align-items: center;
}

.header__burger-button {
  display: none;
}
@media (max-width: 767px) {
  .header__burger-button {
    position: absolute;
    display: block;
    right: 0;
    top: 5px;
    border: none;
    width: 24px;
    height: 12px;
    z-index: 3;
    background-color: transparent;
  }
}
.header__burger-button:hover {
  cursor: pointer;
}

.header__burger-button::before,
.header__burger-button::after {
  content: "";
  background-color: #f9fbfd;
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  transition: all 0.3s ease 0s;
}

.header__burger-button::before {
  top: 0;
}

.header__burger-button::after {
  bottom: 0;
}

.header__burger-button span {
  position: absolute;
  background-color: #f9fbfd;
  left: 0;
  width: 100%;
  height: 2px;
  top: 5px;
  transition: all 0.3s ease 0s;
}

.header-menu--is-active .header__burger-button span {
  transform: scale(0);
}

.header-menu--is-active .header__burger-button::before {
  transform: rotate(45deg);
  top: 5px;
  background-color: #011c40;
}

.header-menu--is-active .header__burger-button::after {
  transform: rotate(-45deg);
  bottom: 5px;
  background-color: #011c40;
}

.header__menu {
  height: auto;
  padding: 0;
  position: static;
  margin-left: auto;
  background-color: transparent;
}
@media (max-width: 767px) {
  .header__menu {
    position: fixed;
    width: 100%;
    top: 0;
    right: -100%;
    padding: 50px 15px 10px;
    max-width: 285px;
    height: 100%;
    background-color: #f9fbfd;
    transition: all 0.3s ease 0s;
  }
  .header__menu::before, .header__menu::after {
    position: fixed;
    content: "";
    right: -100%;
    width: 100%;
    z-index: 10;
    max-width: 285px;
    min-height: 60px;
    background-color: #f9fbfd;
    transition: all 0.3s ease 0s;
  }
  .header__menu::before {
    top: 0;
  }
  .header__menu::after {
    bottom: 0;
  }
}

@media (max-width: 767px) {
  .header-menu--is-active .header__menu {
    right: 0;
    overflow: auto;
  }
  .header-menu--is-active .header__menu::before, .header-menu--is-active .header__menu::after {
    right: 0;
  }
}

@media (max-width: 767px) {
  .header-menu--is-active .menu__link {
